import { createClient } from '@supabase/supabase-js';

const supabaseUrl = process.env.REACT_APP_SUPABASE_URL;
const supabaseAnonKey = process.env.REACT_APP_SUPABASE_ANON_KEY;

// Check if environment variables are configured
if (!supabaseUrl || !supabaseAnonKey) {
  console.error('Supabase environment variables are not configured. Please set REACT_APP_SUPABASE_URL and REACT_APP_SUPABASE_ANON_KEY in your .env file');
}

export const supabase = createClient(supabaseUrl || '', supabaseAnonKey || '');

// Login with email and password
export const loginUser = async (email, password) => {
  try {
    if (!supabaseUrl || !supabaseAnonKey) {
      throw new Error('Supabase is not configured. Please check your environment variables.');
    }

    const { data, error } = await supabase.auth.signInWithPassword({
      email,
      password,
    });

    if (error) {
      throw new Error(error.message);
    }

    return data;
  } catch (error) {
    console.error('Login error:', error);
    throw error;
  }
};

// Logout user
export const logoutUser = async () => {
  try {
    const { error } = await supabase.auth.signOut();
    if (error) {
      throw new Error(error.message);
    }
  } catch (error) {
    console.error('Logout error:', error);
    throw error;
  }
};

// Get current user
export const getCurrentUser = async () => {
  try {
    const { data: { user }, error } = await supabase.auth.getUser();
    if (error) {
      // Don't throw error for missing session, just return null
      return null;
    }
    return user;
  } catch (error) {
    // Don't log error for missing session
    return null;
  }
};

export const deleteUserAccount = async () => {
  try {
    // Check if Supabase is configured
    if (!supabaseUrl || !supabaseAnonKey) {
      throw new Error('Supabase is not configured. Please check your environment variables.');
    }

    // Get the current user's session
    const { data: { session }, error: sessionError } = await supabase.auth.getSession();
    
    if (sessionError) {
      throw new Error(sessionError.message);
    }
    
    if (!session) {
      throw new Error('No active session found. Please log in first.');
    }

    // Call the delete account edge function
    const functionUrl = `${supabaseUrl}/functions/v1/account-deletion`;
    
    const response = await fetch(functionUrl, {
      method: 'POST',
      headers: {
        'Authorization': `Bearer ${session.access_token}`,
        'Content-Type': 'application/json',
      },
    });

    const result = await response.json()

    if (!response.ok) {
      throw new Error(result.error || 'Failed to delete account');
    }

    // Logout user after successful deletion
    await logoutUser();

    return result;
  } catch (error) {
    console.error('Delete account error:', error);
    throw error;
  }
};
